module.exports = {
  popData: {
    showCompanyadd: false,
    showBusinessadd: false,
    companyStatus: true, //  truefalse
    businessStatus: true // truefalse
  },
  popMethod: {
    showCompanyaddoff () {
      this.showCompanyadd = false
    },
    addcompany () {
      this.showCompanyadd = true
    },
    showBusinessaddoff () {
      this.showBusinessadd = false
    },
    addBusiness () {
      this.showBusinessadd = true
    }
  }
}



